import React from "react";
import ReactPlayer from "react-player";
import videData from "../asset/.mp4";
import styled from "styled-components";
import "./VideoCss.css";
const PlayerWrapper = styled.div`
  position: relative;
  width: 100%;
  height: 100%;

  .react-player__control {
    pointer-events: none; /*   */
  }
`;

const Video = () => {
  const handleEndVideo = () => {
    // alert(" ");
  };
  return (
    <PlayerWrapper>
      <ReactPlayer
        url={videData}
        controls={true}
        width="80%"
        height="80%"
        style={{ margin: "0 auto" }}
        onEnded={handleEndVideo}
      />
    </PlayerWrapper>
  );
};

export default Video;
