import React, { useRef, useState, useEffect } from "react";
import videData from "../asset/.mp4";
import "./VideoCustomCss.css";

const VideoTagCustom = () => {
  const videoRef = useRef(null);
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const [isFullscreen, setIsFullscreen] = useState(false);

  //  /  
  const handlePlayPause = () => {
    if (videoRef.current.paused) {
      videoRef.current.play();
      setIsPlaying(true);
    } else {
      videoRef.current.pause();
      setIsPlaying(false);
    }
  };

  //    
  const handleEndVideo = () => {
    alert(" ");
  };

  //    
  const handleTimeUpdate = () => {
    if (videoRef.current) {
      setCurrentTime(videoRef.current.currentTime);
    }
  };

  //    
  const handleLoadedMetadata = () => {
    if (videoRef.current) {
      setDuration(videoRef.current.duration);
    }
  };

  //    
  const handleProgressChange = (event) => {
    const newTime = (event.target.value / 100) * duration;
    if (videoRef.current) {
      videoRef.current.currentTime = newTime;
    }
  };

  //    
  const handleFullscreen = () => {
    if (videoRef.current) {
      if (!document.fullscreenElement) {
        videoRef.current.requestFullscreen();
      } else {
        document.exitFullscreen();
      }
    }
  };

  //      
  useEffect(() => {
    const handleFullscreenChange = () => {
      setIsFullscreen(!!document.fullscreenElement);

      //      
      if (isFullscreen && videoRef.current) {
        videoRef.current.style.webkitAppearance = "none"; // Chrome/Safari
        videoRef.current.style.msAppearance = "none"; // IE
        videoRef.current.style.appearance = "none"; // Other browsers
      }
    };

    document.addEventListener("fullscreenchange", handleFullscreenChange);

    return () => {
      document.removeEventListener("fullscreenchange", handleFullscreenChange);
    };
  }, [isFullscreen]);

  //   
  const removeFocus = () => {
    if (videoRef.current) {
      videoRef.current.blur(); //  .
    }
  };

  // //    
  // const handleTouchStart = (event) => {
  //   if (videoRef.current) {
  //     event.preventDefault(); //   
  //   }
  // };

  useEffect(() => {
    //      
    const videoElement = videoRef.current;

    if (videoElement) {
      videoElement.addEventListener("focus", removeFocus);
      // videoElement.addEventListener("touchstart", handleTouchStart);

      //      
      return () => {
        videoElement.removeEventListener("focus", removeFocus);
        // videoElement.removeEventListener("touchstart", handleTouchStart);
      };
    }
  }, []);

  return (
    <div className={`video-container ${isFullscreen ? "fullscreen" : ""}`}>
      <video
        ref={videoRef}
        id="myVideo"
        src={videData}
        width="50%"
        muted
        onEnded={handleEndVideo}
        onTimeUpdate={handleTimeUpdate}
        onLoadedMetadata={handleLoadedMetadata}
        controls={false} //   
      />
      <div className="custom-controls">
        <button onClick={handlePlayPause}>
          {isPlaying ? "Pause" : "Play"}
        </button>
        <input
          className="videoBar"
          type="range"
          min="0"
          max="100"
          value={(currentTime / duration) * 100 || 0}
          onChange={handleProgressChange}
        />
        <button onClick={handleFullscreen}>
          {isFullscreen ? "Exit Fullscreen" : "Fullscreen"}
        </button>
      </div>
    </div>
  );
};

export default VideoTagCustom;
